
body{
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding-bottom: 1000px;
    overflow-x: hidden;
    padding-right:15px;
}
.top-logo{
  display: flex;
  width: 40px;
  height: 45px;

  z-index: -1;
}

.h-logo{
  width: 50px;
  height: 56px;
}
.options{
    background-color: rgb(255, 247, 237);
    border: 1px solid rgb(255, 247, 237);

}
.chartpage{
  display: flex;
  justify-content: space-between;

}

.s-links {
  position: fixed;
  top: 0;
  color: #00ff00;
  width: 100%;
  background-color: transparent;
  transition: background-color 0.5s ease-out;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px;
  z-index: 1; /* Add this line */
}
.links {
  position: fixed;
  top: 0;
 
  width: 100%;
  background-color: transparent;
  transition: background-color 0.5s ease-out;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px;
  z-index: 1; /* Add this line */
}
.c-top-section {
  display: inline-block;
  vertical-align: top;
  width: 100vw; /* set the width to be the full width of the viewport */
  height: 70vw;
  background-color: #f24952;
}
.s-top-section {
  display: inline-block;
  vertical-align: top;
  width: 100vw; /* set the width to be the full width of the viewport */
  height: 70vw;
  background-color: #1f35ff;
}

.top-section {
  position: absolute;
  width: 100vw; /* set the width to be the full width of the viewport */
  height: 70vw;
  background-color: #8383ff;
}
.top-section-content{
  display: flex;
  justify-content: space-between;
  padding: 2%;
  margin-top: 15%;
  color: black;
}
.c-top-section-content{
  display: flex;
  justify-content: space-between;
  padding: 2%;
  margin-top: 15%;
  color: black;
}
.s-top-section-content{
  display: flex;
  justify-content: space-between;
  padding: 2%;
  margin-top: 15%;
  color: #00ff00;
}
.links.scrolled {
  background-color: white;
  width : 100vw;
  left: 0%;
}
.s-links.scrolled {
  background-color: white;
  color: black;
  width : 100vw;
  left: 0%;
}
.s-title{
  color: #00ff00;
}

h1 {
  font-size: 3.5rem;
  font-weight: 400;
  margin-bottom: -2%;
}
.subtitle {
  display: flex;
  width: 90%;
  font-weight: 200;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5rem;
}

.cta-button {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
  background-color: transparent;
  padding: 0.5rem 1rem;
  color: black;
  border-radius: 0;
  border-color: black;
}
button:focus,
button:active {
  outline: none;
  box-shadow: none;
}
.top-left {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-family: Arial, Helvetica, sans-serif;
  position: absolute;
  top: 6%;
  left: 1%;

}
.top-right {
  position: absolute;
  top: 6%;
  right: 2%;
}
.chart-button{
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    background-color: transparent;
    padding: 0.5rem 1rem;
    border:none;
}
.chart-button:hover{
  color: rgba(38, 0, 255, 1);
}

.stats-button{
    font-size: 1rem;
    background-color: transparent;
    padding: 0.5rem 1rem;
    border:none;
}
.stats-button:hover{
  color: rgba(38, 0, 255, 1);
}
.api-button{
    font-size: 1rem;
    background-color: transparent;
    padding: 0.5rem 1rem;
    border:none;
}
.api-button:hover{
  color: rgba(38, 0, 255, 1);
}
.home-button{
    font-size: 1rem;
    background-color: transparent;
    padding: 0.5rem 1rem;
    border:none;
}
.home-button:hover{
  color: rgba(38, 0, 255, 1);
}
.player-stat-row{
  min-height: 3rem;
}
.chart-container{
  display: block;
  width: 100%;
  padding: 1%;
}
.chart-header{
  position: relative;
  left: 1%;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 2rem;
  color: black;
}
.player-search{
  margin-top: 10px;
  border:rgb(255, 247, 237);
}
.player-search:focus{
  outline: none;
  box-shadow: none;
}
.player-stat-row{
  margin-top: 1%;
  margin-bottom: 1%;
  border: 1px solid rgb(255, 247, 237);
  border-radius: 0;
  padding: 1%;
}
.ant-row{
  border: none;
}
.delete-player{
  text-align: center;
  color: red;
  border-radius: 0;
  border : none;
}
.total-point-stat{
  font-size: 0.8rem;
  color: black;
  padding-top: 3px;
}

/* remove default border and box shadow */
.ant-select-focused.ant-select-single:not(.ant-select-customize-input) .ant-select-selector,
.ant-select-single.ant-select-show-arrow:not(.ant-select-customize-input):hover .ant-select-selector {
  border-color: black;
  box-shadow: none;
}
.ant-slider-handle {
  background-color: black;
  border-color: black;
  size: 100;
}

.c-footer{
  position: relative;
  display: flex;
  width: 100%;
  background-color: #f24952;
  padding: 1%;
  height: 200px;
  margin-bottom: -10%;
  margin-top: 5%;
}
/* Set color of slider tick marks to black */
.ant-slider-mark-text {
  color: black;
}
.ant-slider:focus .ant-slider-handle {
  outline: none;
  box-shadow: none;
}

/* apply custom border */
.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
  border-radius: 0px;
  border: 1px solid black;
}

/* remove focus highlight */
.ant-select-focused.ant-select-single:not(.ant-select-customize-input) .ant-select-selector,
.ant-select-single.ant-select-show-arrow:not(.ant-select-customize-input):hover .ant-select-selector {
  color: inherit;
  border-color: black;
  box-shadow: none;
}
.my-slider .ant-slider-handle {
  border-color: brown !important;
  background-color: rgb(15, 15, 15) !important;
}

.my-slider .ant-slider-handle:focus {
  border-color: brown !important;
  background-color: rgb(15, 15, 15) !important;
}

.my-slider .ant-slider-handle-dragging {
  border-color: brown !important;
  background-color: chartreuse !important;
  outline: none;
}


.back-to-top{
  color: black;
  border-radius: 0;
  left: 2%;
}
.chart-buttons{
  border : 1px solid black;
  background-color: white;
  border-radius: 0px;
  margin-right: 1%;
  margin-top: 1%;
  
}
.h-footer{
  position: absolute;
  bottom: -100%;
  background-color: white;
  color: black;
  padding: 1%;

}
.player-total-stat-name{
  display: flex;
  font-size: 0.9rem;
}
.hp-image{
  width: 100%;
  height: auto;
}
.cp-image{
  margin-top: -20px;
    width: 100%;
    height: auto;
}
.sp-image{
  margin-top: -20px;
    width: 100%;
    height: auto;
}

.ant-slider-handle{
  background-color: black;
}
/* statspage */

.stat-leaders{
  width: 100vw;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 2%;
  background-color: #120c4c;
}
.stat-leaders-title{
  color: #00ff00;
  font-size: 2rem;
  margin-bottom: 10px;
}
.stat-leaders-paragraph{
  width: 40%;
  display: flex;
  color: #ff23a4;
  font-size: 1.4rem;
  margin-left: 2%;
  margin-bottom: 10px;
}
.stat-leaders-content{
  width: 100vw;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

/* statcard */
.stat-leader-card {
  width: 25%;
  min-width: 230px;
  background-color: #1f35ff;
  margin: 10px;
  padding: 10px;
  border-radius: 0px;
}

.stat-leader-title {
  color: #00ff00;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}
.stat-leader-name {
  margin-left: 1%;
  font-size: 1rem;
  color: #ff23a4;
  
}

.stat-leader-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5%;
  background-color: #120c4c;
  padding: 5px;
  align-items: center;

}

.stat-leader-score {
  font-size: 1rem;
  color: #ff23a4;
  margin-left: auto;
}

.stat-leader-avg {
  font-size: 1rem;
  border: 2px solid #00ff00;
  padding: 5px;
  color : #ff23a4; 
  margin-left: auto;
}
@media only screen and (max-width: 750px) {
  /* CSS rules for phones */
  .top-section {
    height: 1200px;
  }
  .c-top-section {
    height: 170vw;
  }
  .s-top-section {
    height: 170vw;
  }
  .top-section-content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1%;
    margin-top: 20%;
  }
  .top-section-content > div {
    justify-content: left;
    width: 100%;
    margin-bottom: 400px;
  }
  .c-top-section-content > div {
    justify-content: left;
    width: 100%;
    margin-bottom: 400px;
  }
  .s-top-section-content > div {
    justify-content: left;
    width: 100%;
    margin-bottom: 400px;
  }

  .hp-image{
    margin-top:-300px;
    position: relative;
    width: 90vw;
    height: auto;
    margin-bottom: 20px;
    left: 4vw;
  }
  .cp-image{
    margin-top:250px;
    position: relative;
    width: 90vw;
    height: auto;
    margin-bottom: 20px;
    left: -45vw;

  }
  .sp-image{
    margin-top:250px;
    position: relative;
    width: 90vw;
    height: auto;
    margin-bottom: 20px;
    left: -45vw;

  }

  .subtitle{
    padding: 0 5%;
    width: 90vw;
  }
  h1{
    padding: 0 5%;
    width: 90vw;
  }
}
